*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 15px;
  line-height: 26px;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #484848; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 1366px;
  margin: 0 auto;
  padding-left: 110px;
  padding-right: 110px; }

.title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1fc0fc; }

.page-header {
  min-height: 895px;
  height: 100vh;
  background-image: url("../img/header-bg-desktop@1x.jpg");
  background-size: 1366px 906px;
  background-position: center top;
  background-repeat: no-repeat; }

.page-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 28px; }

.page-header__logo {
  order: 1;
  margin-top: 2px;
  margin-right: 25px; }

.page-header__phone {
  font: inherit;
  line-height: 18px;
  font-weight: 500;
  color: #484848;
  text-decoration: none; }
  .page-header__phone:hover, .page-header__phone:focus {
    opacity: 0.5; }

.page-header__order {
  order: 1;
  margin-right: 5px;
  font: inherit;
  font-weight: 500;
  line-height: 18px;
  font-variant: small-caps;
  color: #484848;
  text-decoration: none;
  border-bottom: 1px dashed #484848; }
  .page-header__order:hover, .page-header__order:focus {
    opacity: 0.5; }

.page-header__text {
  margin: 0;
  padding: 0;
  margin-top: 452px;
  text-align: center;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff; }

.page-header__button {
  position: relative;
  display: block;
  padding: 0;
  margin: 0 auto;
  margin-top: 32px;
  width: 76px;
  height: 76px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  cursor: pointer;
  background-image: url("../img/mouse-btn.svg");
  background-size: 54px 54px;
  background-repeat: no-repeat;
  background-position: center center;
  outline: none; }
  .page-header__button::before {
    content: "";
    position: absolute;
    top: 100%;
    left: 49%;
    width: 1px;
    height: 56px;
    background-color: #ffffff; }

.page-header__button-circle {
  animation: rotate 6s infinite linear; }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.about {
  min-height: 764px;
  background-image: url("../img/about-programs-watercolor.png");
  background-repeat: no-repeat;
  background-size: 1366px 604px;
  background-position: center 160px; }

.about__title {
  margin: 0;
  padding: 0;
  text-align: center; }

.about__description {
  width: 700px;
  padding: 0;
  margin: 0 auto;
  margin-top: 24px;
  font-size: 17px;
  line-height: 29px;
  text-align: center; }

.about__video {
  position: relative;
  display: block;
  margin: 0 auto;
  margin-top: 50px;
  width: 870px; }

.about__video-img {
  display: block;
  width: 100%; }

.about__button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 96px;
  height: 96px;
  transform: translate(-50%, -55%);
  cursor: pointer; }
  .about__button::before, .about__button::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    opacity: 0.32;
    top: 0;
    left: 0;
    background-color: #ffffff;
    z-index: -1; }
  .about__button::after {
    width: 65%;
    height: 65%;
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .about__button:hover::after, .about__button:focus::after {
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.05); }
  .about__button:active::after {
    background-color: #f4f4f4;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.05); }

.about__button:hover .about__video-btn,
.about__button:focus .about__video-btn {
  fill: #63d3fd; }

.about__button:active .about__video-btn {
  fill: #1fc0fc; }

.about__video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-38%, -50%); }

.programs {
  background-image: url("../../img/programs-bg.png");
  background-repeat: no-repeat;
